.landing {
    height: 92.5vh;

    display: flex;

    align-items: flex-start;
    justify-content: flex-start;

    flex-direction: column;

    background-color: #EEEEEE;
}

.landing .introduction {
    height: 97.5vh;

    display: flex;
    flex-direction: column;

    align-items: flex-start;
    justify-content: center;

    margin-left: 150px;
}

.landing .introduction .title {
    font-family: 'avenir';
    font-size: 64px;
    color: #3E3E3E;

    margin-bottom: 10px;
}

.landing .introduction .subtitle-bold {
    font-family: 'avenir';
    font-weight: bold;
    font-size: 18px;
    color: #3E3E3E;
}

.landing .introduction .subtitle {
    font-family: 'avenir';
    font-style: normal;
    font-size: 18px;
    color: #3E3E3E;
}

.landing .about {
    display: flex;
    align-items: center;
    flex-direction: row;

    margin-top: 100px;
    margin-left: 150px;
}

.landing .about .imageContainer {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 350px;
    height: 350px;
}

.landing .about .image {
    margin-top: 130px;

    object-fit: cover;
    width: 350px;
}

.landing .about .descriptionContainer {
    width: 50vw;
    height: 350px;

    display: flex;
    flex-direction: column;

    margin-left: 50px;
}

.landing .about .descriptionContainer .hey {
    font-family: 'avenir';
    font-size: 36px;
    color: #3E3E3E;

    margin-bottom: 20px;
}

.landing .about .descriptionContainer .text {
    font-family: 'avenir';
    font-style: normal;
    font-size: 16px;

    line-height: 24px;
    margin-bottom: 24px;

    color: #3E3E3E;
}

.landing .about .descriptionContainer .text-bold {
    font-family: 'avenir';
    font-weight: bold;
    font-size: 16px;

    line-height: 24px;
    margin-bottom: 24px;

    color: #3E3E3E;
}

.landing .about .descriptionContainer .contact {
    display: flex;
    flex-direction: row;
}

.landing .about .descriptionContainer .contact .circle {
    margin-right: 10px;

    transition: transform .35s;
}

.landing .about .descriptionContainer .contact .circle:hover {
    transform: scale(1.15);
}

.landing .about .descriptionContainer .contact .logo {
    width: 50px;
}

.polaroid {
  height: 250px;
  width: 200px;
  background: #FFFFFF;
  box-shadow: 6px 6px 6px rgba(0, 0, 0, 0.15);

  display: flex;
  flex-direction: column;;
  align-items: center;
  justify-content: center;

  margin-left: 10px;
  margin-right: 10px;

  transition: transform .3s;

}

.polaroid:hover {
  transform: scale(1.05);
  background: #EEEEEE;
}

.polaroid .text h1 {
  color: #2b2b2b;
  font-size: 16px;
  font-family: avenir;
  font-style: normal;

  margin-top: 0px;
  margin-bottom: 5px;

  display: flex;
  align-items: center;
  justify-content: center;
}

.polaroid .text h2 {
  color: #4b5d52;
  font-size: 14px;
  font-family: avenir;
  font-style: normal;

  margin-top: 0px;
  margin-bottom: 0px;

  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
}

